@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700;900&display=swap);
.my-day{display:grid;grid-gap:32px;gap:32px}.my-day__title{font-size:1.5rem;font-weight:700}.my-day__run-result{font-family:monospace;white-space:pre;font-size:2rem}.my-day__parts{display:grid;grid-auto-flow:column;grid-template-columns:1fr 1fr;grid-gap:24px;gap:24px}.my-day__part{border:2px solid #c08aff;border-radius:4px;padding:32px;display:grid;grid-template-rows:auto auto 1fr;grid-gap:16px;gap:16px}.my-day__part-title{font-size:1.3rem;font-weight:700;text-transform:uppercase;background-color:#16141a;color:#c08aff;padding:0 8px;position:absolute;line-height:16px;transform:translate(-8px, -42px)}.my-day__part-desc{font-size:1rem}.my-day__test-result{display:grid;grid-auto-flow:row;grid-template-columns:repeat(auto-fit, minmax(11em, auto));grid-row-gap:8px;row-gap:8px;justify-items:start;align-content:start}.my-day__data{font-family:monospace;max-width:25vw;overflow:hidden;white-space:pre}.my-day__testline{font-family:monospace}.my-day__testline--is-success{color:#3cc962}.my-day__testline--is-fail{color:#e66064}
.my-picker{display:grid;grid-auto-flow:column;justify-content:center;margin:24px auto;grid-gap:8px;gap:8px;cursor:default}.my-picker__option{font-size:1.3rem;font-weight:700;text-transform:uppercase;border-radius:8px;background-color:rgba(255,255,255,.1);min-width:32px;height:48px;padding:0 16px;display:grid;place-items:center}.my-picker__option--current{background-color:#5b476b}
html{background-color:#16141a;color:#dbcedf}body{margin:0}p{font-size:1rem;margin:0}a{color:#c08aff}a:visited{color:#c08aff}ul{padding:0 0 0 18px;margin:0}
.my-app{font-family:Source Sans Pro;display:grid;grid-template-rows:auto 1fr auto;height:100vh;margin:0 auto;padding:0 64px;max-width:960px;grid-gap:32px;gap:32px}
